import React from 'react';
import s from './Login.module.scss'
import { Field, reduxForm } from 'redux-form';
import {Input, Checkbox} from '../common/Forms/Controls';
import {email, password, requiredField} from "../../utils/validators/validators";
import {Redirect} from "react-router-dom";
import Icon from '../common/Icon'

export default ({isAuth, authLogin, captcha}) => {
    const handleSubmitLogin = formData => authLogin(formData);

    return isAuth
        ? <Redirect to={'/profile'}/>
        : (
            <div className={s.LoginPage}>
                <div className={s.Wrapper}>
                    <h1>Вход</h1>
                    <LoginFormRedux onSubmit={handleSubmitLogin} captcha={captcha}/>
                </div>
            </div>
        );
}

const LoginForm = props => {
    const { handleSubmit, captcha } = props;
    // console.log(props);
    return (
        <form onSubmit={handleSubmit}>
            <Field
                type="email"
                name="email"
                label={'Email'}
                placeholder={'Email'}
                component={Input}
                validate={[email, requiredField]}
            />
            <Field
                type="password"
                name="password"
                label={'Пароль'}
                placeholder={'Пароль'}
                component={Input}
                validate={[requiredField, password]}
            />

            {captcha && <>
                <img src={captcha} alt={''}/>
                <Field
                    name="captcha"
                    label={'Введите код с картинки'}
                    placeholder={'капча'}
                    component={Input}
                    validate={[requiredField]}
                />
            </>
            }

            <Field
                type="checkbox"
                component={Checkbox}
                name="rememberMe"
                label={'Remember me'}
            />

            <div className={s.Row}>
                <button className={s.Button} type="submit">Login</button>
                {props.error && props.error.message && <>
                        <span className={s.Error}>{props.error.message}</span>
                        <Icon icon={'alert'} className={s.ErrorIcon}/>
                    </>
                }
            </div>
        </form>
    )
};
const LoginFormRedux = reduxForm({
    form: 'login'
})(LoginForm);
