import React from 'react'
import s from './Form.module.scss';
import {Field} from "redux-form";

const FormControl = props => {
    const {input: { name }, label, meta: { touched, error }, children} = props;
    const hasError = touched && error;
    const controlClass = s.Row + (hasError ? ' ' + s.FieldError: '');

    return <div className={controlClass}>
        {label && <label htmlFor={name} className={s.Label}>{label}</label>}
        {children}
        {hasError && <span className={s.Error}>* {error}</span>}
    </div>
};

const Input = props => {
    return (
        <FormControl {...props}>
            <input type={props.type} {...props.input} className={s.Input} placeholder={props.placeholder}/>
        </FormControl>
    )
};

const Textarea = (props) => {
    return (
        <FormControl {...props}>
            <textarea {...props.input} className={s.Textarea} placeholder={props.placeholder}/>
        </FormControl>
    )
};

const Checkbox = (props) => {
    const {input, type, label} = props;

    return (
        <div className={s.Row}>
            <label className={s.CheckboxLabel}>
                <input type={type} {...input} className={s.Checkbox}/>
                {label}
            </label>
        </div>
    )
};

export const CreateField = props => {
    return <Field {...props} key={props.name}/>
};

export { Textarea, Input, Checkbox }
