export const requiredField = value => (value ? undefined : 'Поле обязательно');

export const maxLength = max => value => value && value.length > max
    ? `Должен содержать максимум ${max} символа`
    : undefined;
export const maxLength50 = maxLength(50);

export const minLength = min => value => value && value.length < min
    ? `Должен содержать минимум ${min} символа`
    : undefined;
export const minLength3 = minLength(3);
export const minLength6 = minLength(6);

export const email = value => value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
    ? 'Это точно email?'
    : undefined;

export const password = value => value && !/(?=^.{6,}$)((?=.*\d)|(?=.*\W+))(?![.\n])(?=.*[A-Z])(?=.*[a-z]).*$/i.test(value)
    ? `Латинские буквы, цифры, спецсимволы. Минимум 6 символов`
    : undefined;
